.tippy-box {
    
    border-radius: 0;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    max-height: 250px;
    &:not(.nav-tooltip) {
        background-color: white;
        color: black;
        width: $credits;
        overflow: auto;
        margin-left: -4px;
        padding: 22px;
        .tippy-arrow {
            display: none;
        }
    }
    p {
        &:first-of-type {
            margin-top: 0;
        }
        &:last-of-type {
            margin-bottom: 0;
        }
    }
    opacity: 0;
    box-sizing: border-box;
    &[data-state="visible"] {
        opacity: 1;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    }
    
    
}
